import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class GedService {
  private url = "http://localhost:8080/Back-end/ged/";
  private docs: string[] = []; // ['NodeJS', 'JavScript', 'TypeScript', 'Angular', 'Java'];

  constructor(private http: HttpClient) { 

  }

  getAllDocuments(): string[] {
    this.http.get<string []>(this.url + 'docs').subscribe(
      response => {
        this.docs = response;
      }
    )
    return this.docs;
  }
}
