import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AddDocumentComponent } from './ged/add-document/add-document.component';
import { DocumentListComponent } from './ged/document-list';
import { SignInComponent } from '../app/ged/sign-in/sign-in.component'
import { from } from 'rxjs';

const routes: Routes = [
  {path : 'add', component : AddDocumentComponent},
  {path : 'list', component : DocumentListComponent},
  {path : 'sign-in', component : SignInComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
